import React from 'react';
import PropTypes from 'prop-types';

// Components
import { Image } from '../../components';
import { StyledStaff, StyledStaffBlock } from './styles';

export const Staff = ({ data, ...props }) => {
  return (
    <StyledStaff {...props}>
      {data.map((data, index) => {
        const { name, bio, title, imagePath } = data;
        return (
          <StyledStaffBlock key={index}>
            <div>
              <Image src={imagePath} alt={name} />
            </div>
            <div>
              {name && <h4>{name}</h4>}
              {title && <h5>{title}</h5>}
              {bio && <p>{bio}</p>}
            </div>
          </StyledStaffBlock>
        );
      })}
    </StyledStaff>
  );
};

Staff.propTypes = {
  data: PropTypes.array,
};
